    "name": "Kukkakaalicurry",
    "portions": "4",
    "steps": [
      "Hienonna kuorittu sipuli ja kuoritut valkosipulinkynnet. Viipaloi chili ja poista halutessasi siemenet. Revi kukkakaalista kukinnot ja paloittele varsi.",
      "Kuumenna ljy korkeareunaisella pannulla. Lis curry, sipulit ja chili ja kuullota kasvikset mausteljyss.",
      "Kumoa pannulle kukkakaalit ja sekoita. Lis tomaattimurska ja kookosmaito.",
      "Keit currya ilman kantta miedolla lmmll 10-15 minuuttia kunnes kukkakaalit ovat kypsi.",
      "Lis babypinaatit ja ananakset liemineen ja mausta curry suolalla, sokerilla ja mustapippurilla. Kuumenna ruoka ja tarjoa korianterin ja keitetyn riisin kanssa."
    ],
