    "name": "Mehev kasvispiirakka",
    "portions": "8",
    "steps": [
      "Sekoita pohjan kuivat aineet keskenn. Nypi sekaan kylm paloiteltu rasva ryynimiseksi seokseksi. Sekoita joukkoon juustoraaste. Lis kylm vesi nopeasti sekoittaen. Levit taikina voidellun irtopohjavuoan (halkaisija 24 cm) pohjalle ja reunoille. Siirr vuoka jkaappiin tytteen valmistamisen ajaksi.",
      "Kuori ja hienonna sipulit ja valkosipulit. Kuori porkkanat ja raasta ne sek keskurpitsa karkeaksi raasteeksi.",
      "Kuumenna ljy paistokasarissa, lis sipulit, valkosipulit, porkkanat ja keskurpitsa. Kiehauta vihanneksia, kunnes niist irtoava neste on lhes haihtunut. Lis joukkoon paloiteltu tuorejuusto. Mausta suolalla ja anna seoksen hieman jhty.",
      "Sekoita kananmunat ja kerma keskenn. Kaada seos kasvisten joukkoon. Sekoita ja kaada seos vuokaan.",
      "Paista 200 asteessa uunin alimmalla tasolla noin 25 minuuttia. Tarjoa haaleana."
    ],
    "tags": [
      "kasvis"
    ]
